@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sectionMobile {
  min-height: 100vh;
  display: flex;
  justify-content: center;
}

.sectionMobile #container, .sectionMobile #container #titleMobile {
  position: absolute;
}

#bioComponent {
  background-color: antiquewhite
}
.App {
  text-align: center;
}

.App-header {
  background-color: #4c0b12;
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: row; 
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  background-image: url(/static/media/capa.8f260fef.jpg);
  -webkit-filter: invert(15%);
          filter: invert(15%);
  background-position: center;
  background-size: cover;
}

.globo {
  /* display: none; */
  order: 0;
  font-size: 1.7em;
  position: absolute !important;
}

#languagePick {
  order: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  height: 190px;
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  position: absolute;
  align-self: flex-start;
  margin-top: 5vmin;
}

#languagePick div {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  margin: 10px;
}

#languagePick > div:hover {
  cursor: pointer;
  color: slategray;
}


.fa-globe-americas {
  align-self: flex-start;
  position: fixed;
  margin: 5vmin 7vmin 0 0;
}

.fa-globe-americas:hover + #languagePick {
  opacity: 1;
}

#languagePick:hover {
  opacity: 1;
}

.bandeiras {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  margin: 0 2vmin 0 2vmin;
}

.bandeiraEspanha, .bandeiraUSA, .bandeiraItalia {
  width: 58px;
}

.bandeiraEspanha:hover, .bandeiraUSA:hover, .bandeiraItalia:hover {
  width: 64px;
}

.bandeiraBrasil{
  width: 54px;
} 

.bandeiraBrasil:hover {
  width: 58px;
}

#bioSection {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#bioText {
    order: 0;
    text-align: left;
    text-align: initial;
    font-size: 11vmin;
    padding: 0 12vmin;
}

#autor {
    order: 1;
    font-size: 4vmin;
    padding: 0 12vmin;
    text-align: right;
}
.albunsCapas {
    background: -webkit-linear-gradient(bottom right, #ffa17f, #00223e);
    background: linear-gradient(to top left, #ffa17f, #00223e);
    display: flex;
    align-items: center;
    justify-content: center;
}

.fp-section {
    text-align: center
}

.capas {
    width: 60vmin;
    height: 60vmin;
}

.fab {
    font-size: calc(15px + 6vmin);
    margin: 5vmin 5vmin 0 5vmin
}

.fab.fa-spotify:hover {
    color: #10793f
}

.fab.fa-youtube:hover {
    color: #d65757
}

.fab.fa-facebook:hover {
    color: #8bc1f3
}

.fab.fa-instagram:hover {
    color: rgb(206, 167, 61)
}

.fab.fa-github:hover {
    color: #536577;
}

.btn-social:hover {
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    cursor: pointer;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

.mobile {
    width: 100%;
    background-color: rgba(63, 81, 181, 0.5);
}

.slideMobile {
    -webkit-transform: rotate3d(6, 25, 0, 46deg);
            transform: rotate3d(6, 25, 0, 46deg);
    position: absolute;
    box-shadow: -4px -4px 2px 2px antiquewhite;
}

.mobile #capaApego {
    z-index: 3;
    right: 7vmin;
    margin-top: 26vmin;
}

.mobile #capaCcufg {
    z-index: 2;
    right: 20vmin;
    margin-top: 19vmin;
}

.mobile #capaEp {
    z-index: 1;
    right: 33vmin;
    margin-top: 12vmin;
}

.card {
    display: -webkit-inline-box;
    width: 60vmin;
    height: 60vmin;
    border-radius: 25px;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    box-shadow: 1px 3px 3px rgba(0,0,0,0.2);
  }

.card:hover {
    cursor: pointer;
}
  
.card-contents {
    width: 100%;
    height: 100%;
    border-radius: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }

.rotated {
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
}

.card-front {         
    -webkit-transform-style: preserve-3d;         
            transform-style: preserve-3d;
    background-position: center;
    background-size: cover;
}

.card-back {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    background: -webkit-linear-gradient(bottom right, #97afb9, #00223e);
    background: linear-gradient(to top left, #97afb9, #00223e);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.card-depth {
    -webkit-transform: translateZ(100px) scale(0.98);
            transform: translateZ(100px) scale(0.98);
    -webkit-perspective: inherit;
            perspective: inherit;
}

#capaApego .card-front{
    background-image: url(/static/media/capaApego.35a0488c.jpg); 
}

#capaCcufg .card-front{
    background-image: url(/static/media/aoVivoCCUFG_cortada.0c48c534.jpg); 
}

#capaEp .card-front{
    background-image: url(/static/media/lutreEpCapa_n.ff27f97d.jpg); 
}
#container {
    margin: 0;
    padding: 0;
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    align-items: center;
}

#map {
    height: 100%;
    width: 100%;
    float: right;
    position: absolute !important;
	-webkit-overflow-scrolling: auto;
}

#parteSemMapa {
    display: flex;
    height: 100%;
    width: 50vmin;
    float: left;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 1;
    background-color: rgba(255, 255, 255, 0.6);
}

#title {
    height: 5vmin;
}

#addMarcadorNoBancoBtn {
    display: flex;
    height: 6vmin;
    width: 90%;
    margin-bottom: 6vmin;
    justify-self: flex-end
}

#toggleVisaoMapa {
    align-self: flex-start;
    z-index: 2;
    position: absolute;
    width: 43px;
    display: flex;
    height: 38px;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    right: 0;
    margin: 7px;
    background-color: #fff;
}

#iconVisaoNoturna {
    color: #242f3e;
}

#iconVisaoRetro {
    color: #dbb449;
}

#titleMobile, 
#addMarcadorNoBancoBtnMobile, 
#divAddMarcadorNoBancoBtnMobile {
    display: none;
}

@media (max-width: 584px) {
    #container {
        height: 100%;
    }
    
    #map {
        height: 100%;
        position: absolute !important;
        width: 100%;
        -webkit-overflow-scrolling: auto;
    }
    
    #titleMobile {
        width: 100%;
        height: 55px;
        z-index: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        background-color: #3f51b5;
        color: #fff;
        font-family: "Roboto", "Helvetica", "Arial", sans-serif;
        font-weight: 500;
        font-size: 17px;
        text-transform: uppercase;    
        align-self: flex-start;
    }

    #divAddMarcadorNoBancoBtnMobile {
        display: flex;
        width: 100%;
        justify-content: center;
    }

    #addMarcadorNoBancoBtnMobile {
        display: flex;
        width: 170px;
        position: absolute;
        bottom: 0;
        margin-bottom: 7vmin;
        z-index: 1;
        font-size: 16px;
        border-radius: 26px;
    }

    #toggleVisaoMapa {
        margin: 62px 7px 0 0;
    }
}
#contatoSection{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.divIcon {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
}

#fb {
    background: -webkit-linear-gradient(135deg, #4267b2, #7197e4, #0a2763, #022770);
    background: linear-gradient(-45deg, #4267b2, #7197e4, #0a2763, #022770);
	background-size: 400% 400%;
	-webkit-animation: Gradient 4s ease infinite;
	animation: Gradient 4s ease infinite;
} 

#ig {
    background: -webkit-linear-gradient(135deg, #EE7752, #E73C7E, rgb(204, 151, 37), rgb(35, 76, 213));
    background: linear-gradient(-45deg, #EE7752, #E73C7E, rgb(204, 151, 37), rgb(35, 76, 213));
	background-size: 600% 600%;
	-webkit-animation: Gradient 4s ease infinite;
	animation: Gradient 4s ease infinite;
}

#sp {
    background: -webkit-linear-gradient(135deg, #1e7d15, #a0ee9b, #3aa779, #42e084);
    background: linear-gradient(-45deg, #1e7d15, #a0ee9b, #3aa779, #42e084);
	background-size: 400% 400%;
	-webkit-animation: Gradient 4s ease infinite;
	animation: Gradient 4s ease infinite;
}

#yt {
    background: -webkit-linear-gradient(135deg, #e77777, #b14444, #bb4b4b, #dc133f);
    background: linear-gradient(-45deg, #e77777, #b14444, #bb4b4b, #dc133f);
	background-size: 400% 400%;
	-webkit-animation: Gradient 4s ease infinite;
	animation: Gradient 4s ease infinite;
} 

#gh {
    background-color: #536577;
    background: -webkit-linear-gradient(135deg, #536577, #78a7d6, #294f75, #1174d6);
    background: linear-gradient(-45deg, #536577, #78a7d6, #294f75, #1174d6);
	background-size: 400% 400%;
	-webkit-animation: Gradient 4s ease infinite;
	animation: Gradient 4s ease infinite;
}


@-webkit-keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

@keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}
